header {
  nav {
    ul {
      @include flex(space-evenly, center);
      & > li {
        font-size: 22px;
        font-style: italic;
      }
    }
  }
}
.content_page {
  width: 100%;
  white-space: pre-wrap;
  h1 {
    color: $blue;
    font-size: 36px;
    font-weight: bold;
    margin-bottom: 15px;
  }
}
h2 {
  color: $primaryColor;
  font-size: 26px;
  font-weight: 600;
}
pre {
  font-weight: 600;
  padding: 15px;
  background-color: $grayLight;
}
.content_page {
  padding: 50px;
  width: 70%;
  margin: $mxAuto;
}
.btn {
  display: inline-block;
  border-radius: 5px;
  border: none;
  padding: 10px;
  margin-top: 10px;
  background-color: $secondaryColor;
  color: white;
  font-size: 18px;
  cursor: pointer;
}
.file_form {
  width: 80%;
  margin: auto;
  text-align: center;
  form {
    text-align: left;
  }
  label {
    display: block;
    margin-top: 30px;
  }
  input,
  select,
  textarea {
    width: 100%;
  }
  span.red {
    margin-left: 5px;
    color: red;
  }
}
.article {
  border: 1px solid #000;
  margin: 25px 0;
  padding: 10px;
  .comments {
    border: 1px solid #000;
    margin: 10px;
    padding: 10px;
  }
  .comment:not(:last-child) {
    border-bottom: 1px solid #000;
  }
}
.article_action {
  @include flex(flex-start, center);
}
.disconnect {
  background-color: blue;
  color: $white;
  font-weight: 600;
  padding: 35px 10px;
}
.connect {
  position: relative;
  cursor: pointer;
}
.modal_connect {
  display: none;
  position: absolute;
  background-color: #fff;
  padding: 20px;
  border: 2px solid #000;
  border-radius: 10px;
}
.red {
  color: red;
}
.green {
  color: green;
}
.cursor_pointer {
  cursor: pointer;
}
.comment {
  @include flex(space-between, center);
  .comment_action {
    @include flex(center, center, column);
  }
  .fa-solid {
    text-align: center;
    margin: 10px;
    font-size: 20px;
  }
}
.pop_delete_article {
  display: none;
  position: fixed;
  padding: 30px;
  background-color: #fff;
  border: 2px #000 solid;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  transition: all ease-out 0.4s;
  .delete_action_article {
    margin-top: 20px;
    @include flex(flex-start, center);
  }
}
.dBlock {
  display: block;
}
